___CSS_LOADER_EXPORT___.push([module.id, ".input{margin-top:30px;width:300px}.popup{position:fixed;top:50%;left:50%;transform:translate(-50%,-50%);background:#fff;display:flex;justify-content:center;width:33.33%;height:300px}.center{align-self:center;padding:2rem}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Transactions/SendingTransaction.vue"],"names":[],"mappings":"AAkMA,OACI,eAAgB,CAChB,WACJ,CAEA,OACI,cAAe,CACf,OAAQ,CACR,QAAS,CACT,8BAAgC,CAChC,eAAiB,CACjB,YAAa,CACb,sBAAuB,CACvB,YAAa,CACb,YACJ,CAEA,QACI,iBAAkB,CAClB,YACJ","sourcesContent":["<template>\n    <app-layout>\n        <template #header>\n            <h2 class=\"font-semibold text-xl text-gray-800 leading-tight\">\n                Sending money\n            </h2>\n        </template>\n        <div v-if=\"$page.props.code || finishing || $page.props.errors.code\" class=\"popup\">\n            <div class=\"center\">\n                <div v-if=\"$page.props.errors.code\" class=\"text-red-500\"> Incorrect code</div>\n                Check your email for verification code\n                <form @submit.prevent=\"verifyCode\">\n                    <input class=\"input\"\n                           v-model=\"verify.code\"\n                           name=\"verificationCode\"\n                           type=\"text\"\n                           placeholder=\"verificationCode\"\n                           required>\n                    <div>\n                        <button class=\"btn btn-primary mt-2\"\n                                :disabled=\"verify.code.length < 20\"> Verify\n                        </button>\n                    </div>\n                </form>\n            </div>\n        </div>\n        <div v-else class=\"py-12\">\n            <div class=\"max-w-7xl mx-auto sm:px-6 lg:px-8\">\n\n                <div>\n                    <div style=\"margin-bottom: -24px\"\n                         v-if=\"form.senderAccount.amount === null \">Please select account\n                    </div>\n                    <form method=\"post\" @submit.prevent=\"validate\">\n                        <select class=\"input\"\n                                v-model=\"form.senderAccount\"\n                                name=\"senderAccount\"\n                                id=\"senderAccount\"\n                                required\n                                @change=\"excludingSenderAccount\">\n                            <option v-for=\"account in bankAccounts\"\n                                    :key=\"account\"\n                                    :value=\"{number: account.number, amount: account.amount, type: account.type}\">\n                                {{ account.type === 0 ? 'Debit' : 'Saving' }}:\n                                {{ account.number }} {{ formatCurrency(account.amount, account.currency) }}\n                            </option>\n                        </select>\n                        <div v-if=\"form.senderAccount.amount !== null\">\n                            <div>\n                                <input class=\"input\"\n                                       v-model=\"form.receiver\"\n                                       name=\"receiver\"\n                                       type=\"text\"\n                                       placeholder=\"Receiver\"\n                                       required>\n                            </div>\n                            <div v-if=\"form.errors.receiver\"\n                                 style=\"margin-bottom: -24px\"\n                                 class=\"text-red-500\">Receiver name is not associated to bank account\n                            </div>\n                            <div v-if=\"form.senderAccount.type === 1\">\n                                <select class=\"input\"\n                                        v-model=\"form.receiverAccount\"\n                                        name=\"receiverAccount\"\n                                        id=\"receiverAccount\"\n                                        required>\n\n                                    <option v-for=\"account in excludeSenderAccount\"\n                                            :key=\"account\"\n                                            :value=\"account.number\">\n                                        {{ account.type === 0 ? 'Debit' : 'Saving' }}:\n                                        {{ account.number }} - {{ formatCurrency(account.amount, account.currency) }}\n                                    </option>\n                                </select>\n                            </div>\n                            <div v-else><input v-model=\"form.receiverAccount\"\n                                               name=\"receiverAccount\"\n                                               class=\"input\"\n                                               type=\"text\"\n                                               placeholder=\"Receiver account number\"\n                                               minlength=\"20\"\n                                               maxlength=\"20\"\n                                               required>\n                            </div>\n                            <div v-if=\"form.errors.receiverAccount\"\n                                 style=\"margin-bottom: -24px\" class=\"text-red-500\">\n                                Invalid account, please check again!\n                            </div>\n                            <div><input\n                                v-model=\"form.sendingAmount\"\n                                name=\"sendingAmount\"\n                                id=\"sendingAmount\"\n                                class=\"input\"\n                                type=\"number\"\n                                placeholder=\"Amount\"\n                                :disabled=\"amountToLow\"\n                                step=\".01\"\n                                :max=\"form.senderAccount.amount/100\"\n                                required>\n                                <div v-if=\"form.errors.sendingAmount\"\n                                     style=\"margin-bottom: -24px\"\n                                     class=\"text-red-500\"> Your don`t have that much money\n                                </div>\n                            </div>\n                            <div>\n                                <input class=\"input\"\n                                       v-model=\"form.purpose\"\n                                       name=\"purpose\"\n                                       type=\"text\"\n                                       placeholder=\"Purpose of payment\"\n                                       required>\n                            </div>\n                            <button style=\"margin-top: 10px\"\n                                    class=\"btn btn-primary\"\n                                    :disabled=\"amountToLow || $page.props.code\">\n                                Send\n                            </button>\n                        </div>\n                    </form>\n                </div>\n            </div>\n        </div>\n    </app-layout>\n</template>\n\n<script>\nimport AppLayout from \"@/Layouts/AppLayout\";\nimport {useForm} from '@inertiajs/inertia-vue3'\n\nexport default {\n    props: ['user', 'code'],\n    components: {\n        AppLayout\n    },\n    data() {\n        return {\n            finishing: false,\n            bankAccounts: [],\n            excludeSenderAccount: [],\n            form: this.$inertia.form({\n                senderAccount: {\n                    amount: null,\n                    number: null,\n                    type: null,\n                },\n                sendingAmount: null,\n                receiverAccount: null,\n                purpose: null,\n                receiver: null,\n            }),\n            verify: this.$inertia.form({\n                code: '',\n                userIndex: this.$props.user.id\n            })\n        }\n    },\n    beforeMount() {\n        this.getData()\n    },\n    methods: {\n        getData() {\n            axios.get('/api/getUserAccounts/').then((response) => {\n                this.bankAccounts = response.data\n            })\n        },\n        validate() {\n            this.form.post('/api/validation/')\n        },\n        verifyCode() {\n            this.verify.post('/api/validateVerification/', {\n                onSuccess: () => {\n                    this.form.post('/api/sendMoney/')\n                },\n            })\n        },\n        formatCurrency(money, currency) {\n            return new Intl.NumberFormat('en-US', {style: 'currency', currency: currency}).format(money / 100)\n        },\n    },\n    computed: {\n        amountToLow() {\n            if (this.form.senderAccount.amount <= 0) {\n                this.form.sendingAmount = null\n            }\n            return this.form.senderAccount.amount <= 0\n        },\n        excludingSenderAccount() {\n            this.excludeSenderAccount = _.cloneDeep(this.bankAccounts)\n            this.excludeSenderAccount = this.excludeSenderAccount.filter(account => account.number !== this.form.senderAccount.number)\n        }\n    }\n}\n</script>\n<style>\n.input {\n    margin-top: 30px;\n    width: 300px;\n}\n\n.popup {\n    position: fixed;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    background: white;\n    display: flex;\n    justify-content: center;\n    width: 33.33%;\n    height: 300px;\n}\n\n.center {\n    align-self: center;\n    padding: 2rem;\n}\n</style>\n"],"sourceRoot":""}]);
