___CSS_LOADER_EXPORT___.push([module.id, ".input[data-v-41fdd25f]{margin-top:10px;width:300px}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Accounts/Create.vue"],"names":[],"mappings":"AAmFA,wBACI,eAAgB,CAChB,WACJ","sourcesContent":["<template>\n    <app-layout>\n        <template #header>\n            <h2 class=\"font-semibold text-xl text-gray-800 leading-tight\">\n                Open new account\n            </h2>\n        </template>\n        <div class=\"py-12\">\n            <div class=\"max-w-7xl mx-auto sm:px-6 lg:px-8\">\n                <form @submit.prevent=\"create\">\n                    <div>\n                        <jet-label for=\"type\" value=\"Choose account type\"/>\n                        <select class=\"input\"\n                                name=\"type\"\n                                id=\"type\"\n                                v-model=\"form.type\"\n                                required>\n                            <option value=\"0\">Debit account</option>\n                            <option value=\"1\">Saving account</option>\n                        </select></div>\n                    <jet-label for=\"type\" value=\"Choose currency\"/>\n                    <div><select name=\"currency\"\n                                 id=\"currency\"\n                                 v-model=\"form.currency\"\n                                 required>\n                        <option value=\"EUR\">EUR</option>\n                        <option v-for=\"currency in currencies\"\n                                :key=\"currency\"\n                                :value=\"currency.name\">\n                            {{ currency.name }}\n                        </option>\n                    </select></div>\n                    <button class=\"btn btn-primary mt-6\"\n                            type=\"submit\">Submit</button>\n                </form>\n            </div>\n        </div>\n    </app-layout>\n</template>\n\n<script>\nimport AppLayout from \"@/Layouts/AppLayout\";\nimport {useForm} from \"@inertiajs/inertia-vue3\";\nimport JetLabel from \"@/Jetstream/Label\";\n\nexport default {\n    data() {\n        return {\n            currencies: []\n        }\n    },\n    components: {\n        AppLayout,\n        JetLabel\n    },\n    setup() {\n        const form = useForm({\n            type: 0,\n            currency: 'EUR',\n        })\n        return {form}\n    },\n    methods:\n        {\n            create() {\n                this.form.post('/create')\n            },\n            getCurrencies() {\n                axios.get('/api/getCurrencies/')\n                    .then(res => {\n                        res.data.shift()\n                        this.currencies = res.data\n                    })\n                    .catch(error => console.log(error.message));\n            }\n        },\n    beforeMount() {\n        this.getCurrencies()\n    }\n}\n</script>\n\n<style scoped>\n.input {\n    margin-top: 10px;\n    width: 300px;\n}\n</style>\n"],"sourceRoot":""}]);
